body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(right, rgb(20, 52, 78)0%,rgb(71, 206, 177));
  background: linear-gradient(to left, rgb(20, 52, 78)0%,rgb(71, 206, 177));
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
@font-face {
    font-family: 'SEGA LOGO FONT';
    font-style: normal;
    font-weight: normal;
    src: local('SEGA LOGO FONT'), url(/robofriends/static/media/SEGA.12a3db8b.woff) format('woff');
    }
h1{
    font-family:'SEGA LOGO FONT';
    font-weight: 200;
    color: #0ccac4;
   
}
